/* https://coolors.co/palette/e63946-f1faee-a8dadc-457b9d-1d3557 */
:root {
  --c1: rgb(230, 57, 70);
  --c2: rgb(241, 250, 238);
  --c3: rgb(168, 218, 220);
  --c4: rgb(69, 123, 157);
  --c5: rgb(29, 53, 87);

  --off: rgb(255, 120, 120);
  --on: rgb(140, 255, 140);

  --ondark: rgb(80, 205, 80);
  --offdark: rgb(205, 80, 80);
}


html, body {
  /*height: 100vh;
  overflow: hidden;*/
}

.App {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  color: var(--c5);
}

.emoji-button {
  border: none;
  background: none;
  padding: 0;
  outline: none;
}


.page {

}

.pad {
  padding: 1rem;
}

.page-title {
  background: var(--c5);
  color: white;
  margin: 0;
  font-size: 2rem;
  padding: 0.5rem 1rem;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.3);
}


.BigButtons {
  padding: 1rem;
  width: 100%;
}

.BigButtons-button {
  display: block;
  font-size: 1.25rem;
  width: 100%;
  padding: 1rem;
  border-radius: 4px;
  border: 1px solid #ccc;
  background: var(--c3);
}

.BigButtons-button[disabled], .BigButtons-button[disabled]:active {
  opacity: 0.6;
}

.BigButtons-button:active {
  opacity: 0.8;
}

.BigButtons-button + .BigButtons-button {
  margin: 1rem auto 0;
}

.BigButtons-button.dangerous {
  background: var(--c1);
  color: white;
}

.BigButtons-button.alt {
  background: var(--c2);
}

